
#sign {
  position: relative;
  width: 100%;
  height: 100%;
  display: block;
}
#title {
  position: relative;
  width: 60%;
  height: 100%;
  float: left;
  display: inline-block;
  margin: 10% 0%;
}
#textbox {
  position: relative;
  width: 30%;
  height: 100%;
  float: left;
  display: inline-block;
  margin: 15% 0%;
}
.demo-popup-top {
  width: 100%;
  opacity: .8;
  height: 48px;
  line-height: 48px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  max-width: 375px;
}



