
.demo-step-container {
    position: relative;
    width: 100%;
    height: 100%;
    display: block;
}
#title {
    position: relative;
    width: 60%;
    height: 100%;
    float: left;
    display: inline-block;
    margin: 10% 0%;
}
#content {
    width: 30%;
    height: 100%;
    position: relative;
    display: inline-block;
    margin: 16% 0%;
}
#title h1 {
    color: darkgrey;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 85px;
}
#title h2 {
    color: darkgrey;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 20px;
}
#content.demo-step-content {
    margin: 0 16px;
}
#content.demo-step-button {
    margin-top: 12px;
    margin-right: 12px;
}



